.cooking-page-title{
    font-family: Jokerman, sans-serif;
}

.recipes-list{
    background: white;
    color: black;
    border-radius: 20px;
    font-family: "Comic Sans MS", sans-serif;
}

.cake-picture{
    width: 100px;
    height: auto;
    border-radius: 30px;
}

.table-recipe-titles > tbody > tr > th{
    padding: 20px;
    margin: 20px;
}

.recipe-title-button{
    background: transparent;
    border: none;
    font-family: "Comic Sans MS", "Comic Sans", cursive, sans-serif;
    font-size: 30px;
    cursor: pointer;
}

.recipe-title-button:hover{
    font-size: 35px;
}

.recipe-title-button:active{
    transform: translateY(4px);
}